import { EntityPage, EntityPageState, useEntityPage } from '@wix/patterns';
import { useForm } from '../../exports/form';
import React from 'react';
import { EntityPageConfig, CardLayout, LayoutContent } from '../../types';

export interface SkeletonEntityProps {
  entityPage: EntityPageConfig;
}
import { Card, Cell, Layout } from '@wix/design-system';

const RenderLayoutContent: React.FC<{
  content: LayoutContent;
  level: number;
  sectionId: string;
  cardIndex: number;
  contentIndex: number;
}> = ({ content, level, sectionId, cardIndex, contentIndex }) => {
  switch (content.type) {
    case 'container':
      if (!content.container.children?.length) {
        return null;
      }

      return (
        <Cell span={content.container.span}>
          <Layout>
            {content.container.children.map((child, index) => (
              <RenderLayoutContent
                key={`${sectionId}-card-${cardIndex}-content-${contentIndex}-level-${level}-child-${index}`}
                content={child}
                level={level + 1}
                sectionId={sectionId}
                cardIndex={cardIndex}
                contentIndex={index}
              />
            ))}
          </Layout>
        </Cell>
      );

    default:
      return null;
  }
};

const RenderLayoutCard: React.FC<{
  layout: CardLayout;
  sectionId: string;
  cardIndex: number;
}> = ({ layout, sectionId, cardIndex }) => {
  switch (layout.type) {
    case 'card':
      return (
        <EntityPage.Card minHeight="50px">
          <Card.Header
            title={layout.card.title.text}
            subtitle={layout.card.subtitle?.text}
          />
          <Card.Divider />
          <Card.Content>
            <Layout>
              {layout.card.children.map((child, index: number) => (
                <RenderLayoutContent
                  key={`${sectionId}-card-${cardIndex}-content-${index}`}
                  content={child}
                  level={0}
                  sectionId={sectionId}
                  cardIndex={cardIndex}
                  contentIndex={index}
                />
              ))}
            </Layout>
          </Card.Content>
        </EntityPage.Card>
      );
    default:
      return null;
  }
};

export const SkeletonEntity = ({ entityPage }: SkeletonEntityProps) => {
  const form = useForm();

  const state: EntityPageState<any> = useEntityPage<any, any>({
    parentPath: '',
    form,
    onSave: async () => ({ updatedEntity: {} }),
    fetch: async () => new Promise(() => {}),
  });

  return (
    <>
      <EntityPage state={state}>
        <EntityPage.Header title={{ text: '' }} />
        <EntityPage.Content>
          <EntityPage.MainContent>
            {entityPage.layout?.main?.map((layout, layoutIndex) => (
              <RenderLayoutCard
                key={`main-section-card-${layoutIndex}`}
                layout={layout}
                sectionId="main"
                cardIndex={layoutIndex}
              />
            ))}
          </EntityPage.MainContent>
          {entityPage.layout?.sidebar ? (
            <EntityPage.AdditionalContent>
              {entityPage.layout.sidebar?.map((layout, layoutIndex) => (
                <RenderLayoutCard
                  key={`sidebar-section-card-${layoutIndex}`}
                  layout={layout}
                  sectionId="sidebar"
                  cardIndex={layoutIndex}
                />
              ))}
            </EntityPage.AdditionalContent>
          ) : null}
        </EntityPage.Content>
      </EntityPage>
    </>
  );
};
